/*--------------Newsletter sign up form-------------------*/
form input {
	margin: 15px auto;
	display: block;
	font-size: 1.2vw;
	width: 100%;
  
  border-style: none;
  border-bottom: solid;
  border-color: #777b7c;
  border-width: 2px;
	padding: 5px;
	font-family: "Montserrat", sans-serif;
	width: 50%;
}

form button {
  background-color: #e85f5c;
  color: #ffffff;
	font-size: 1.3vw;
	width: 100%;
	margin: 20px auto;
	border: none;
	display: block;
	padding: 10px;
  letter-spacing: 3px;
	text-transform: uppercase;
	width: 50%;
}

.newsletterImg {
	width: 40%;
	object-fit: contain;
}

.paper {
	background-color: white;
	border-radius: 2;
	padding: 10;
	padding: 1vw 2vw 2vw 2vw;
	width: 100%;
}

.formTitle {
	color: #feb14b;
	font-size: 2.5vw;
	font-family: "Montserrat", sans-serif;
	letter-spacing: 3px;
	text-transform: uppercase;
	text-align: center;
}

.formDescription {
	color: #313638;
	font-family: "Montserrat", sans-serif;
	font-size: 12pt;
	margin-left: auto;
	margin-right: auto;	
	text-align: center;
}

.msg-alert p {
  font-size: 1.3vw;
}

.closeBtn {
	float: right;
}

.closeSymbol {
	color: #313638;
	text-align: right;
}

.subscribeError {
  color:#e85f5c;
  font-weight: 500;
}

/*--------------Responsive Mobile Design-------------------*/
@media only screen and (max-width: 1000px) {
	form button {
		font-size: 12pt;
		width: 75%;
	}
	
	form input {
		width: 75%;
	}

	.formTitle {
		font-size: 14pt;
  }
  
  form input, .msg-alert p {
    font-size: 12pt;
  }
}

@media only screen and (max-width: 600px) {  
  form input, .msg-alert p {
    font-size: 11pt;
	}
	
	form input, .formDescription, form button {
		width: 80%;
	}
}
